

import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

export default {
components: {
ViewBox,
XHeader,
Group,
XInput
},
data () {
return {
index: 0,
cardNum: '',
name: ''
}
},
mounted () {
this.init()
},
computed: {








},
methods: {
init () {
this.name = this.$store.state.userInfo.name
},
scanCard () {
let self = this
let data = {
action: 'jump_bankcard_get'
}
self.$store.dispatch('normalRequest', data).then(res => {
if (res.response === 'success') {
self.cardNum = res.cardNo
}
})
},
addCard () {
let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/bindBankCard.do'
let data = {
action: 'init_request',
path: path,
params: {
userToken: self.$store.state.userInfo.token,
chName: this.name,
cardNo: this.cardNum
}
}
if (this.cardNum === '') {
this.$vux.toast.text('')
return null
}
self.$store.dispatch('normalRequest', data).then(response => {
if (response.response === 'success') {
this.$vux.toast.text('')
this.cardNum = ''
this.$router.replace('/bankCardList')
} else {
this.$vux.toast.text(response.data)
}
})
},
addBankCard () {
let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/getAppBankCardList.do'
let data = {
action: 'init_request',
path: path,
params: {
userToken: 'e2e9e2dc-07c6-41f0-9b80-0486a1c0f5b4'
}
}
self.$store.dispatch('normalRequest', data).then(res => {
})
}
}
}



